import * as React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import Seo from "../components/seo"

const StorePage = ({data}) => {
    const storeInfo = data.allMarkdownRemark.nodes[0].frontmatter
    return (
  <Layout>
    <Seo title="Sore/Hours" />
    <section className="page-section">
            <div className="container cta">
                <div className="row">
                    <div className="col-xl-9 mx-auto">
                        <div className="cta-inner bg-faded text-center rounded">
                            <h2 className="section-heading mb-5">
                                <span className="section-heading-upper">{storeInfo.subtitle}</span>
                                <span className="section-heading-lower">{storeInfo.title}</span>
                            </h2>
                            <ul className="list-unstyled list-hours mb-5 text-left mx-auto">
                                <li className="list-unstyled-item list-hours-item d-flex">
                                    Sunday
                                    <span className="ms-auto">11:00 AM to 4:00 PM</span>
                                </li>
                                <li className="list-unstyled-item list-hours-item d-flex">
                                    Monday
                                    <span className="ms-auto">10:00 AM to 7:00 PM</span>
                                </li>
                                <li className="list-unstyled-item list-hours-item d-flex">
                                    Tuesday
                                    <span className="ms-auto">10:00 AM to 7:00 PM</span>
                                </li>
                                <li className="list-unstyled-item list-hours-item d-flex">
                                    Wednesday
                                    <span className="ms-auto">10:00 AM to 7:00 PM</span>
                                </li>
                                <li className="list-unstyled-item list-hours-item d-flex">
                                    Thursday
                                    <span className="ms-auto">10:00 AM to 7:00 PM</span>
                                </li>
                                <li className="list-unstyled-item list-hours-item d-flex">
                                    Friday
                                    <span className="ms-auto">10:00 AM to 7:00 PM</span>
                                </li>
                                <li className="list-unstyled-item list-hours-item d-flex">
                                    Saturday
                                    <span className="ms-auto">10:00 AM to 7:00 PM</span>
                                </li>
                            </ul>
                            <p className="address mb-5">
                                <em>
                                    <strong>{storeInfo.address.street}</strong>
                                    <br />
                                    {storeInfo.address.city}, {storeInfo.address.state} {storeInfo.address.zip}
                                </em>
                            </p>
                            <p className="mb-0">
                                <small><em>{storeInfo.phone.title}</em></small>
                                <br />
                                {storeInfo.phone.number}
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </section>
  </Layout>
)
    }

export default StorePage

export const pageQuery = graphql`
query StoreDetails {
    allMarkdownRemark(
      filter: {frontmatter: {type: {eq: "data"}, for: {eq: "storeInfo"}}}
    ) {
      nodes {
        id
        frontmatter {
          title
          subtitle
          hours {
            day
            time
          }
          address {
            city
            state
            street
            zip
          }
          phone {
            number
            title
          }
        }
      }
    }
  }
  
` 